import React from 'react';
import moment from 'moment';
import { Link } from 'react-router-dom';

export default function ToyPreview(props) {
    const { toy } = props;

    return (
        <div className="toy-card flex column">
            <h2>{toy.name}</h2>
            <h2>Price: ${toy.price}</h2>
            <h2>[ {toy.type} ]</h2>
            <h2>{moment(toy.createdAt).format('LL')}</h2>
            <h2>In stock: {toy.inStock ? 'Yes' : 'No'}</h2>
            <img src={toy.imgUrl ? toy.imgUrl[0] : "assets/img/bear.png"} />
            <Link to={`/toy/${toy._id}`}>Details</Link>
        </div>
    )
}