import React from 'react'
import moment from 'moment'

export default class ToyChat extends React.Component {

    state = {
        isExpanded: false,
        msg: '',
        msgs: [
            { txt: 'Hello there!', by: 'robot', createdAt: Date.now() }
        ]
    }

    handleSubmit = (ev) => {
        ev.preventDefault()
        const msg = this.state.msg
        this.setState(prevState => ({ msgs: [...prevState.msgs, { txt: msg, by: 'user', createdAt: Date.now() }], msg: '' }))
        setTimeout(() => {
            this.setState(prevState => ({ msgs: [...prevState.msgs, { txt: 'Sure thing sweetie...', by: 'robot', createdAt: Date.now() }] }))
        }, 1000)
    }

    handleChange = ({ target }) => {
        this.setState({ msg: target.value })
    }

    toggleChat = () => {
        this.setState(prevState => ({ isExpanded: !prevState.isExpanded }))
    }

    render() {
        const { isExpanded, msg, msgs } = this.state

        return (!isExpanded ? <div className="toy-chat-icon" onClick={this.toggleChat}>Chat</div> :
            <div className="toy-chat flex column">
                <header className="toy-chat-header">
                    <h2>Toy Chat</h2>
                    <button onClick={this.toggleChat}>X</button>
                </header>
                <main className="toy-chat-main">
                    {msgs.map((m, idx) => {
                        return (
                            <div key={idx} className={m.by === 'user' ? 'user-msg' : 'robot-msg'}>
                                <h5 >{m.txt}</h5>
                                <h6>{moment(m.createdAt).startOf().fromNow()}</h6>
                            </div>)

                    })}
                </main>
                <footer className="toy-chat-footer flex">
                    <form onSubmit={this.handleSubmit} className="flex">
                        <input type="text" value={msg} placeholder="How can i help?" onChange={this.handleChange} />
                        <button>Send</button>
                    </form>
                </footer>
            </div>
        )
    }
}


