import React from 'react'
import { Doughnut } from 'react-chartjs-2';
import { connect } from 'react-redux'
import { setToys } from '../actions/toyActions.js'


class ToyStatistics extends React.Component {

    state = {
        data: []
    }

    componentDidMount() {
        this.props.setToys()
            .then(() => this.calcStatistics())
    }

    calcStatistics() {
        const toys = this.props.toys
        let adult = 0, educational = 0, funny = 0, adultPrice = 0, educationalPrice = 0, funnyPrice = 0;

        toys.forEach(toy => {
            if (toy.type === 'Adult') {
                adult++;
                adultPrice += toy.price
            } else if (toy.type === 'Educational') {
                educational++;
                educationalPrice += toy.price;
            } else {
                funny++;
                funnyPrice += toy.price;
            }
        });

        this.setState({ data: [(adultPrice / adult).toFixed(2), (educationalPrice / educational).toFixed(2), (funnyPrice / funny).toFixed(2)] })
    }

    get chartData() {
        return {
            labels: [
                'Adult',
                'Educational',
                'Funny'
            ],
            datasets: [{
                data: this.state.data,
                backgroundColor: [
                    '#FF6384',
                    '#36A2EB',
                    '#FFCE56'
                ],
                hoverBackgroundColor: [
                    '#FF6384',
                    '#36A2EB',
                    '#FFCE56'
                ]
            }]
        }
    }


    render() {
        return (
            <div>
                <h2>Average Prices per toy type:</h2>
                <Doughnut height={100} data={this.chartData} />
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        toys: state.toyApp.toys
    }
}

const mapDispatchToProps = {
    setToys
}

export default connect(mapStateToProps, mapDispatchToProps)(ToyStatistics)