import React from 'react'
import OpenSelect from './OpenSelect';


export default class ToyFilter extends React.Component {

    state = {
        filter: '',
        name: '',
        inStock: false,
        type: '',
        sort: ''

    }

    handleFilter() {
        let { name, inStock, type, sort } = this.state
        inStock = inStock || '';
        this.props.onFilter({ name, inStock, type, sort })
    }

    handleChange = ({ target }) => {
        const { name, value } = target;
        if (name !== 'inStock') this.setState({ [name]: value }, this.handleFilter)
        else this.setState(prevState => ({ inStock: !prevState.inStock }), this.handleFilter)
    }

    render() {
        const { name, type, sort } = this.state
        return (
            <React.Fragment>
                <input className="filter-search-box" autoComplete="off" placeholder="Search" value={name} name="name" onChange={this.handleChange} />
                <label>In stock:</label>
                <input type="checkbox" name="inStock" onChange={this.handleChange} />

                <OpenSelect value={type} options={[{ val: "", name: "All" }, { val: "Adult", name: "Adult" },
                { val: "Educational", name: "Educational" }, { val: "Funny", name: "Funny" }]} name="type" label="Type"
                    handleChange={this.handleChange} />
                <OpenSelect value={sort} options={[{ val: "", name: "None" }, { val: "name", name: "Name" }, { val: "price", name: "Price" }]} name="sort" label="Sort by" handleChange={this.handleChange} />
            </React.Fragment>
        )
    }
}
