import React from 'react'
import { uploadImg } from '../services/cloudinaryService.js'
import { setToy, saveToy } from '../actions/toyActions.js'
import { connect } from 'react-redux'



class ToyEdit extends React.Component {

    state = {
        name: '',
        price: '',
        inStock: true,
        type: 'Adult',
        imgUrl: [],
        isImgLoading: false
    }

    componentDidMount() {
        this.loadToy()
    }

    componentDidUpdate(prevProps) {

        if (prevProps.toy !== this.props.toy) this.setState({ ...this.props.toy });
    }

    loadToy() {
        const { id } = this.props.match.params;
        if (!id) return;
        this.props.setToy(id)
    }

    handleChange = (ev) => {
        let { name, value } = ev.target;
        value = ev.target.type === 'number' ? parseInt(value) : value;
        if (name === 'inStock') value = (value === 'true')
        this.setState({ [name]: value })
    }

    handleSubmit = (ev) => {
        ev.preventDefault()
        const toy = this.state
        this.props.saveToy(toy)
            .then(res => {
                this.props.history.push('/toy')
            })
    }

    uploadImg = (ev) => {
        this.setState({ isImgLoading: true })
        uploadImg(ev)
            .then(res => {
                this.setState(prevState => ({ imgUrl: [...prevState.imgUrl, ...res] }))
                this.setState({ isImgLoading: false })
            })
            .catch(() => this.setState({ isImgLoading: false }))
    }

    onRemoveImg = (ev, index) => {
        ev.preventDefault();
        this.setState(prevState => ({
            imgUrl: [...prevState.imgUrl.filter((url, idx) => {
                if (idx !== index) return url;
            })]
        }))
    }

    render() {
        const { name, price, inStock, type, imgUrl, isImgLoading } = this.state
        const { id } = this.props.match.params

        return (
            <div className="edit-container">
                <form className="flex column" onSubmit={this.handleSubmit}>
                    <h2>{id ? 'Edit' : 'Add'}</h2>
                    <input type="text" placeholder="name" value={name} name="name" onChange={this.handleChange} required />
                    <input min="0" type="number" placeholder="price" value={price} name="price" onChange={this.handleChange} required />
                    <label>In stock:
                    <select value={inStock} name="inStock" onChange={this.handleChange}>
                            <option value={true}>Yes</option>
                            <option value={false}>No</option>
                        </select>
                    </label>
                    <select value={type} name="type" onChange={this.handleChange}>
                        <option value="Adult">Adult</option>
                        <option value="Educational">Educational</option>
                        <option value="Funny">Funny</option>
                    </select>
                    <label className="edit-choose-pic"> Choose Picture(s)
                            <input onChange={this.uploadImg} type="file" multiple hidden />
                    </label>
                    {isImgLoading && <h3>loading...</h3>}
                    {(imgUrl && imgUrl.length !== 0) && <div className="edit-pic-container flex">
                        {imgUrl.map((url, idx) => {
                            return <div key={idx}>
                                <button onClick={(ev) => this.onRemoveImg(ev, idx)}>X</button>
                                <img className="edit-preview-img" src={url} />
                            </div>
                        })}
                    </div>}
                    <button>Save</button>
                </form>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        toy: state.toyApp.currToy
    }
}
const mapDispatchToProps = {
    setToy,
    saveToy
}

export default connect(mapStateToProps, mapDispatchToProps)(ToyEdit)