import React from 'react'
import { Map, InfoWindow, Marker, GoogleApiWrapper } from 'google-maps-react';

export class MapContainer extends React.Component {
  state = {
    places: [
      {
        name: 'Tel Aviv-Yafo',
        pos: {
          lat: 32.0804808,
          lng: 34.7805274
        }
      },
      {
        name: 'Rishon LeZion',
        pos: {
          lat: 31.9635712,
          lng: 34.8101149
        }
      },
      {
        name: 'Ramat Gan',
        pos: {
          lat: 32.0686867,
          lng: 34.8246812
        }
      }
    ],
    currPos: {
      lat: 32.0804808,
      lng: 34.7805274
    }
  };

  onPlaceClick(pos) {
    this.setState({ currPos: pos })
    console.log(this.state)
  }
  containerStyle = {
    position: 'relative',
    width: '100%',
    height: '500px'
  }
  render() {
    const { places, currPos } = this.state
    return (
      <div >
        <div className="branches-container flex justify-center align-center">
          <h2>Branches:</h2>
          <button className="branch-btn" onClick={() => this.onPlaceClick(places[0].pos)}>Tel Aviv</button>
          <button className="branch-btn" onClick={() => this.onPlaceClick(places[1].pos)}>Rishon Lezion</button>
          <button className="branch-btn" onClick={() => this.onPlaceClick(places[2].pos)}>Ramat Gan</button>
        </div>
        <Map containerStyle={this.containerStyle} google={this.props.google} style={{ width: '100%', height: '500px' }}
          initialCenter={currPos} center={currPos} zoom={14}>

          {places.map((place, idx) => {
            return (
              <Marker
                key={idx}
                name={place.name}
                position={place.pos} />
            )
          })}

        </Map>
      </div >
    );
  }
}
export default GoogleApiWrapper({
  apiKey: ('AIzaSyCe7-aDf0TUxFU8VwGrjeQaiqDo56LZ8is')
})(MapContainer)