import React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { setToys } from '../actions/toyActions.js'
import ToyList from '../cmps/ToyList.jsx'
import ToyFilter from '../cmps/ToyFilter.jsx';

class ToyApp extends React.Component {
    state = {
        filter: ''
    }

    componentDidMount() {
        this.loadToys()
    }

    loadToys() {
        this.props.setToys(this.state.filter)
    }

    onFilter = (filter) => {
        this.setState({ filter }, this.loadToys)

    }


    render() {

        const { toys } = this.props

        return (
            <section className="toy-app-container">
                <div className="add-toy-container flex justify-center">
                    <Link className="edit-btn" to="/toy/edit">Add Toy</Link>
                </div>
                <div className="filter-container flex justify-center align-center">
                    <ToyFilter onFilter={this.onFilter} />
                </div>
                {toys && <ToyList toys={toys} />}
            </section>
        )
    }
}


const mapStateToProps = (state) => {
    return {
        toys: state.toyApp.toys
    }
}
const mapDispatchToProps = {
    setToys
}

export default connect(mapStateToProps, mapDispatchToProps)(ToyApp)