import React from 'react'
import { setToy, removeToy } from '../actions/toyActions.js'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom';
import moment from 'moment'

class ToyDetails extends React.Component {

    state = {}

    componentDidMount() {
        const { id } = this.props.match.params;
        this.props.setToy(id)
    }

    onRemove(id) {
        this.props.removeToy(id)
            .then(() => this.props.history.push('/toy'))

    }


    render() {
        const { toy } = this.props

        return (!toy ? <h3>Loading...</h3> :
            <div className="flex justify-center">
                <div className="details-container flex column space-around align-center">
                    <h2>{toy.name}</h2>
                    <h2>Price: ${toy.price}</h2>
                    <h2>{moment(toy.createdAt).format('LL')}</h2>
                    <h2>In stock: {toy.inStock ? 'Yes' : 'No'}</h2>
                    {toy.imgUrl ? toy.imgUrl.map((url, idx) => {
                        return <img key={idx} className="edit-preview-img" src={url} />}) : 
                        <img src={toy.imgUrl || "assets/img/bear.png"} alt="No image set yet" />}

                    <div>
                        <button className="delete-btn" onClick={() => this.onRemove(toy._id)}>Delete</button>
                        <Link className="edit-btn" to={`/toy/edit/${toy._id}`}>Edit</Link>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        toy: state.toyApp.currToy
    }
}
const mapDispatchToProps = {
    setToy,
    removeToy
}

export default connect(mapStateToProps, mapDispatchToProps)(ToyDetails)