import React from 'react';
import './styles/global.scss';
import { Route, Switch } from 'react-router-dom'
import ToyApp from './pages/ToyApp.jsx'
import NavBar from './cmps/NavBar.jsx'
import Home from './pages/Home.jsx'
import ToyDetails from './pages/ToyDetails.jsx'
import ToyEdit from './pages/ToyEdit.jsx'
import ToyChat from './cmps/ToyChat.jsx'
import About from './pages/About.jsx'

function App() {

  let chat = ['Hello there!']


  return (
    <div>
      <header>
        <NavBar />
      </header>
      <main>
        <ToyChat/>
        <Switch>
          <Route component={ToyEdit} path="/toy/edit/:id?" />
          <Route component={ToyDetails} path="/toy/:id" />
          <Route component={ToyApp} path="/toy" />
          <Route component={About} path="/about" />
          <Route component={Home} path="/" />
        </Switch>
      </main>
    </div>
  );
}

export default App;
